.Settings-logo {
    -webkit-animation: Settings-icon-spin infinite 15s linear;
            animation: Settings-icon-spin infinite 15s linear;
}

@-webkit-keyframes Settings-icon-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Settings-icon-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



